body {
  margin: 0;
  background-color: #eacdc2;
  overflow: auto;
}

#container {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#calculator {
  width: 400px;
  height: auto;
  padding: 40px;
  background-color: #757780;
  border-radius: 10px;
}

#display-container {
  white-space: pre-wrap;
  word-wrap: break-word;
  text-align: right;
  padding: 10px;
  min-height: 140px;
  vertical-align: text-top;
  font-family: "VT323", monospace;
  color: #fff8f0;
  background-color: #02020b;
  border-radius: 10px;
}

#display-upper-line {
  font-size: 20px;
}

#display {
  font-size: 30px;
}

#buttons-container {
  height: 480px;
  padding-top: 15px;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  grid-template-rows: 20% 20% 20% 20% 20%;
}

input {
  font-family: "Staatliches", cursive;
  font-size: 22px;
  margin: 5px;
  color: #fff8f0;
  background-color: #45464a;
}

input:hover {
  background-color: #28282a;
}

input:active {
  background-color: #eacdc2;
}

#equals {
  background-color: #db9d47;
  color: #fff8f0;
}

#equals:hover {
  background-color: #aa7122;
}

#equals:active {
  background-color: #eacdc2;
}

#clear {
  grid-column: 1 / 4;
  background-color: #92140c;
  color: #fff8f0;
}

#clear:hover {
  background-color: #4b0b06;
}

#clear:active {
  background-color: #eacdc2;
}

#add {
  grid-column: 4 / 5;
  grid-row: 4 / 6;
  background-color: #3423a6;
  color: #fff8f0;
}

#divide,
#multiply,
#subtract {
  background-color: #3423a6;
  color: #fff8f0;
}

#add:hover,
#divide:hover,
#multiply:hover,
#subtract:hover {
  background-color: #201565;
}

#add:active,
#divide:active,
#multiply:active,
#subtract:active {
  background-color: #eacdc2;
}

@media (width < 768px) {
  #calculator {
    width: 250px;
    padding: 20px;
  }
  #display-container {
    min-height: 100px;
  }

  #display-upper-line {
    font-size: 18px;
  }

  #display {
    font-size: 25px;
  }
  #buttons-container {
    height: 300px;
  }
  input {
    font-size: 18px;
  }
}
